import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { ProductItemDetailComponent } from '../product-item-detail/product-item-detail.component';

@Component({
  selector: 'app-product-item',
  templateUrl: './product-item.component.html',
  styleUrls: ['./product-item.component.css']
})
export class ProductItemComponent implements OnInit {
  firstName = '';
  lastName = '';

  constructor() { }

  @Output() eventSend = new EventEmitter();

  ngOnInit(): void {
  }

  detail: ProductItemDetailComponent = new ProductItemDetailComponent();

  sendData() {
    this.detail.firstNameDetail = this.firstName;
    this.detail.lastNameDetail = this.lastName;
    this.eventSend.emit(this.detail);
    this.firstName = '';
    this.lastName = '';
  }

}
